import React, { useCallback, useState, memo } from "react";
import { useAppSelector, useAppDispatch } from "@/reducers/hooks";
import classNames from "classnames";
import { setLocalCurrent } from "@/reducers/local/slice";

const nav = ["", "", "", ""];

const Local = () => {
  const dispatch = useAppDispatch();
  const [visibility, setVisibility] = useState(false);
  const { current } = useAppSelector(({ local }) => local);
  const handleToggleVisibility = useCallback(() => {
    setVisibility(!visibility);
  }, [visibility]);
  return (
    <div className="domManage">
      <div className="domManage_header ui_header">
        <span className="domManage_header_title h1"></span>
        <span className="domManage_header_total">0</span>
        <button
          type="button"
          className="domManage_header_selectFolder"
          onClick={handleToggleVisibility}
        >
          
        </button>
      </div>
      <div className="domManage_main">
        <div className="domManage_nav">
          {nav.map((item) => (
            <button
              onClick={() => dispatch(setLocalCurrent({ current: item }))}
              key={item}
              className={classNames(
                "domManage_nav_link focus:outline-none",
                item === current && "ui_underline font-bold"
              )}
              type="button"
            >
              {item}
            </button>
          ))}
        </div>
        <div className="domManage_noFolder">
          <div className="domManage_noFolder_title"></div>
          <div className="domManage_noFolder_tips">
            
          </div>
          <button
            type="button"
            className="domManage_noFolder_button"
            onClick={handleToggleVisibility}
          >
            
          </button>
        </div>
        <div
          className="domManage_dialog"
          style={{ display: visibility ? "" : "none" }}
        >
          <button
            type="button"
            className="close"
            onClick={handleToggleVisibility}
          >
            
          </button>
          <div className="title"></div>
          <div className="tips"></div>
          <ul>
            <li>
              <span className="checkbox">
                <input type="text" />
                <i className="material-icons">checkbox</i>
              </span>
              
            </li>
          </ul>
          <div className="actions">
            <button
              type="button"
              className="ui_btn inline-flex items-center justify-center border px-3 h-8 rounded-full red"
            >
              
            </button>
            <button
              type="button"
              className="ui_btn inline-flex items-center justify-center border px-3 h-8 rounded-full"
            >
              
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default memo(Local);
